.more {
  position: relative;
  padding-top: 100px;
  letter-spacing: 10px;
  height: 130vh;
}

.more h1 {
  letter-spacing: 20px;
  font-size: 2.86vw;
  font-weight: 200;
}

.line1 {
  transition: 0.7s;
  width: 0;
  margin: 0 -91px;
  background: rgb(0, 0, 0);
  height: 0.1px;
  border: 0;
}
.long {
  transition: 1s;
  width: 450px;
}
.flex {
  display: flex;
  justify-content: space-between;
}
.flex2 {
  display: flex;
  justify-content: right;
  line-height: 5;
}

.txtBox {
  position: relative;
  top: 200px;
  margin-right: 20vw;
}
.txtBox h2 {
  font-size: 1.56vw;
  font-weight: 300;
}

.txtBox h3 {
  font-size: 1.56vw;
  font-weight: 300;
  margin: 7vw auto;
}

.title {
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(-100px) !important;
}
.link {
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(100px) !important;
}
.flex2 img {
  margin-right: 10vw;
}
.flex2 a:nth-child(1) {
  position: relative;
  left: 15vw;
  color: inherit;
  font-size: 3vw;
  font-weight: 200;
}
.flex2 a:nth-child(2) {
  position: relative;
  left: 15vw;
  color: inherit;
  font-size: 3vw;
  font-weight: 200;
}

.strong1,
.strong2,
.strong3 {
  display: inline-block; /* 또는 display: block; */
}

.strong1 {
  animation: jump 1s ease-in-out infinite;
}

.strong2 {
  animation: jump 1s ease-in-out infinite;
  animation-delay: 0.1s; /* 0.1초 지연 */
}

.strong3 {
  animation: jump 1s ease-in-out infinite;
  animation-delay: 0.2s; /* 0.2초 지연 */
}
.txt1 {
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(-100px) !important;
}
.txt2 {
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(100px) !important;
}
.txt3 {
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(-100px) !important;
}

.fadeIn {
  opacity: 1 !important;
  transform: translateX(0px) !important;
}
@keyframes jump {
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0px);
  }
}
@media screen and (max-width: 600px) {
  .more {
    height: 100vh;
    overflow: hidden;
  }
  .more h1 {
    width: 150%;
    letter-spacing: 10px;
    font-size: 18px;
    font-weight: 200;
  }

  .line1 {
    transition: 0.7s;
    width: 0;
    margin: 0 -91px;
    background: rgb(0, 0, 0);
    height: 1px;
    border: 0;
  }
  .long {
    transition: 1s;
    width: 220px;
  }
  .flex {
    display: block;
  }
  .flex2 {
    display: block;
    text-align: right;
    position: relative;
    right: 15vw;
    line-height: 2;
    top: 119px;
  }

  .txtBox {
    text-align: center;
    position: relative;
    margin: auto 0;
  }
  .txtBox h2 {
    font-size: 15px;
    letter-spacing: 8px;
    font-weight: 300;
  }

  .txtBox h3 {
    font-size: 15px;
    letter-spacing: 8px;
    font-weight: 300;
  }

  .title {
    transition: 0.5s;
    opacity: 0 !important;
    transform: translateX(-100px) !important;
  }
  .link {
    transition: 0.5s;
    opacity: 0 !important;
    transform: translateX(100px) !important;
    text-align: center;
    line-height: 3;
  }
  .flex2 a {
    text-decoration: none;
    color: inherit;
    border-bottom: 1px solid;
    text-align: left;
  }
  .flex2 a:nth-child(1) {
    font-size: 20px;
    font-weight: 200;
  }
  .flex2 a:nth-child(2) {
    font-size: 20px;
    font-weight: 200;
  }

  .strong1,
  .strong2,
  .strong3 {
    display: inline-block; /* 또는 display: block; */
  }

  .strong1 {
    animation: jump 1s ease-in-out infinite;
  }

  .strong2 {
    animation: jump 1s ease-in-out infinite;
    animation-delay: 0.1s; /* 0.1초 지연 */
  }

  .strong3 {
    animation: jump 1s ease-in-out infinite;
    animation-delay: 0.2s; /* 0.2초 지연 */
  }
  .txt1 {
    transition: 0.5s;
    opacity: 0 !important;
    transform: translateX(-100px) !important;
  }
  .txt2 {
    transition: 0.5s;
    opacity: 0 !important;
    transform: translateX(100px) !important;
  }
  .txt3 {
    transition: 0.5s;
    opacity: 0 !important;
    transform: translateX(-100px) !important;
  }

  .fadeIn {
    opacity: 1 !important;
    transform: translateX(0px) !important;
  }
}
