.footer {
  border-top: 0.5px solid;
  height: 150px;
  margin: 0 -90px;
  text-align: center;
}

.footer h1 {
  letter-spacing: 30px;
  font-size: 2vw;
  font-weight: 200;
  line-height: 5;
}

.footer a {
  font-size: 2vw;
  font-weight: 200;
  letter-spacing: 15px;
  position: fixed;
  bottom: 50px;
  right: 30px;
  text-decoration: none;
  color: inherit;
  border-bottom: 1px solid;
  opacity: 1;
  transition: 0.5s;
}
.flex {
  display: flex;
  justify-content: space-evenly;
}

.scrolled a {
  opacity: 0;
}
@media screen and (max-width: 600px) {
  .footer {
    border-top: 0.5px solid;
    height: 100px;
    margin: 0 -20px;
    text-align: center;
    position: relative;
    z-index: 2;
  }

  .footer h1 {
    letter-spacing: 10px;
    font-size: 18px;
    font-weight: 200;
    line-height: 5;
  }

  .footer a {
    font-size: 12px;
    font-weight: 200;
    letter-spacing: 10px;
    position: fixed;
    z-index: 1;
    bottom: 50px;
    right: 20px;
    text-decoration: none;
    color: inherit;
    border-bottom: 1px solid;
    opacity: 1;
    transition: 0.5s;
  }
  .flex {
    display: flex;
    justify-content: space-evenly;
  }
  .scrolled a {
    opacity: 0;
  }
}
