.stack {
  padding-top: 100px;
  letter-spacing: 20px;
  position: relative;
}
.stack h1 {
  position: relative;
  left: 150px;
  font-size: 3.38vw;
  font-weight: 200;
}
.title {
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(-100px) !important;
}
.info {
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(50px) !important;
}
.stackTitle {
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(-100px) !important;
}

.line1 {
  transition: 0.7s;
  width: 0px;
  margin: 0 -91px;
  background: rgb(0, 0, 0);
  height: 0.1px;
  border: 0;
}
.long {
  width: 450px;
}
.stack h2 {
  font-size: 1.2vw;
  font-weight: 300;
  margin-top: 40px;
  margin-left: 27vw;
}

.flex {
  display: flex;
  justify-content: left;
}

.stackBox {
  text-transform: uppercase;
}

.stackBox h1 {
  font-size: 2vw;
  font-weight: 300;
  padding: 0;
  margin: 0;
  margin-left: 15.6vw;
}
.stackBox2 {
  letter-spacing: 2px;
  text-align: right;
  width: 30vw;
  transition: 0.5s;
  opacity: 0 !important;
  transform: translateX(50px) !important;
}
.stackBox2 h2 {
  font-size: 0.93vw;
  width: 100%;
  margin: 0;
  padding: 0;
  margin-top: 10px;
  margin-bottom: 20px;
  text-align: left;
}
.flex2 {
  display: flex;
  justify-content: space-between;
  margin-top: 100px;
}
.line2 {
  width: 110%;
  background: rgb(0, 0, 0);
  height: 0.1px;
  border: 0;
}

.txtBox {
  margin-top: 200px;
  overflow: hidden;
  padding-bottom: 1px;
}
.fadeIn {
  opacity: 1 !important;
  transform: translateX(0px) !important;
}
@media screen and (max-width: 600px) {
  .stack {
    letter-spacing: 10px;
    position: relative;
    overflow: hidden;
  }
  .stack h1 {
    position: relative;
    left: 50px;
    width: 100%;
    font-size: 18px;
    font-weight: 200;
    margin-top: 30px;
  }

  .line1 {
    transition: 0.7s;
    width: 0px;
    margin: 0 -91px;
    background: rgb(0, 0, 0);
    height: 1px;
    border: 0;
  }
  .long {
    width: 220px;
  }
  .stack h2 {
    font-size: 14px;
    font-weight: 300;
    margin-top: 40px;
    margin-left: 27vw;
  }
  .info {
    position: absolute;
    width: 100%;
    left: -80px;
  }
  .flex {
    display: block;
    justify-content: left;
  }

  .stackBox {
    text-transform: uppercase;
  }

  .stackBox h1 {
    font-size: 18px;
    font-weight: 300;
    padding: 0;
    margin: 0 -50px;
  }
  .stackBox2 {
    position: relative;
    top: 50px;
    letter-spacing: 0.5px;
    width: 100%;
    transition: 0.5s;
    opacity: 0 !important;
    transform: translateX(50px) !important;
  }
  .stackBox2 h2 {
    font-size: 14px;
    margin-top: 10px;
    margin-bottom: 15px;
    margin-left: 0;
  }
  .flex2 {
    display: block;
    margin-top: 100px;
    text-align: center;
  }
  .line2 {
    margin-top: 30px;
    width: 100%;
    background: rgb(0, 0, 0);
    height: 1px;
    border: 0;
  }

  .txtBox {
    margin-top: 100px;
    padding-bottom: 60px;
  }
  .fadeIn {
    opacity: 1 !important;
    transform: translateX(0px) !important;
  }
}
