.project {
  letter-spacing: 15px;
  position: relative;
  overflow: hidden;
  padding-bottom: 200px;
}
.Box1In {
  opacity: 1 !important;
  transform: translateX(0px) !important;
}
.project h1 {
  letter-spacing: 20px;
  font-size: 3.3vw;
  font-weight: 200;
}

.mainTitle {
  transform: translateX(-100px);
  transition: 0.5s !important;
  opacity: 0;
}
.mainTitle2 {
  position: relative;
  left: 15vw;
  transform: translateX(100px);
  transition: 0.5s !important;
  opacity: 0;
}

.project h2 {
  font-size: 1.25vw;
  font-weight: 300;
  margin-top: 40px;
}

.line1 {
  position: relative;
  top: 4.1vw;
  transition: 0.7s;
  width: 0px;
  margin: 0 -91px;
  background: rgb(0, 0, 0);
  height: 0.1px;
  border: 0;
}
.long {
  width: 450px;
}
.flex {
  display: flex;
  justify-content: left;
}

.imgBox {
  position: relative;
  top: 350px;
  margin-left: 360px;
  letter-spacing: 10px;
  padding-bottom: 100px;
}

.flex2 {
  display: flex;
  justify-content: space-between;
}

.Box1 {
  position: relative;
  margin-right: 2.6vw;
  opacity: 0;
  transform: translateX(-200px);
  transition: 0.5s;
}
.Box1 img {
  width: 500px;
  height: 400px;
}
.Box2 {
  position: relative;
  top: -100px;
  margin-left: 2.6vw;
  opacity: 0;
  transform: translateX(200px);
  transition: 0.5s;
}
.Box2 img {
  width: 600px;
  height: 500px;
}
.Box4 {
  position: relative;
  margin-left: 50px;
  opacity: 0;
  transform: translateX(200px);
  transition: 0.5s;
}
.Box4 img {
  width: 500px;
  height: 400px;
}
.Box3 {
  position: relative;
  top: -100px;
  margin-right: 50px;
  opacity: 0;
  transform: translateX(-200px);
  transition: 0.5s;
}
.Box3 img {
  width: 600px;
  height: 500px;
}

.stack {
  display: flex;
  justify-content: center;
}

.stack p {
  font-weight: bold;
}

.swiper {
  width: 30vw; /* 30% of the viewport width */
  max-width: 500px; /* 최대 너비 설정 */
  z-index: 1;
  --swiper-theme-color: #00000000;
  border: 1px solid;
}
.swiper2 {
  width: 28vw; /* 30% of the viewport width */
  max-width: 600px; /* 최대 너비 설정 */
  z-index: 1;
  --swiper-theme-color: #00000000;
  border: 1px solid;
}

.icon {
  color: #000;
  position: relative;
  font-size: 35px;
}

.active .txtBox {
  transform: translateY(0px);
  transition: 0.5s;
}
.allClick {
  font-size: 1.25vw;
  cursor: pointer;
  position: relative;
  top: 8vw;
  left: 50vw;
}
.txtBox {
  transform: translateY(-300px);
  transition: 0.5s;
  letter-spacing: 5px;
  line-height: 2;
  text-align: center;
}
.txtBox h3 {
  font-size: 2.5vw;
  font-weight: 300;
  border-bottom: 1px solid;
}
.txtBox h4 {
  font-size: 1.5vw;
  font-weight: 200;
}
.active2 .txtBox {
  transform: translateY(0px);
  transition: 0.5s;
}
.active3 .txtBox {
  transform: translateY(0px);
  transition: 0.5s;
}
.active4 .txtBox {
  transform: translateY(0px);
  transition: 0.5s;
}

.strong1,
.strong2,
.strong3,
.strong4 {
  display: inline-block; /* 또는 display: block; */
}

.strong1 {
  animation: jump 1s ease-in-out infinite;
}

.strong2 {
  animation: jump 1s ease-in-out infinite;
  animation-delay: 0.1s; /* 0.1초 지연 */
}

.strong3 {
  animation: jump 1s ease-in-out infinite;
  animation-delay: 0.2s; /* 0.2초 지연 */
}

.strong4 {
  animation: jump 1s ease-in-out infinite;
  animation-delay: 0.3s; /* 0.3초 지연 */
}

@keyframes jump {
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0px);
  }
}
@media screen and (max-width: 600px) {
  .project {
    padding-bottom: 250px;
  }
  .Box1In {
    opacity: 1 !important;
    transform: translateX(0px) !important;
  }
  .project h1 {
    letter-spacing: 10px;
    width: 100%;
    font-size: 18px;
    font-weight: 200;
  }
  .flex {
    display: block;
  }

  .mainTitle {
    transform: translateX(-100px);
    transition: 0.5s !important;
    opacity: 0;
  }
  .mainTitle2 {
    position: relative;
    transform: translateX(100px);
    transition: 0.5s !important;
    opacity: 0;
  }

  .project h2 {
    letter-spacing: 5px;
    font-size: 14px;
    font-weight: 300;
    margin-top: 100px;
    position: relative;
    left: 0;
    text-align: center;
  }
  .project h4 {
    position: relative;
    left: 0;
    right: 0;
    margin: 0 auto;
    width: 100px;
  }
  .line1 {
    position: relative;
    top: 25px;
    transition: 0.7s;
    width: 0px;
    margin: 0 -91px;
    background: rgb(0, 0, 0);
    height: 1px;
    border: 0;
  }
  .long {
    width: 220px;
  }

  .imgBox {
    position: relative;
    margin-left: 0;
    letter-spacing: 5px;
  }

  .flex2 {
    display: block;
  }

  .Box1 {
    position: relative;
    margin-right: 0;
    opacity: 0;
    transform: translateX(-200px);
    transition: 0.5s;
  }
  .Box1 img {
    width: 500px;
    height: 100%;
  }
  .Box2 {
    position: relative;
    margin: 0;
    margin-top: 200px;
    opacity: 0;
    transform: translateX(200px);
    transition: 0.5s;
  }
  .Box2 img {
    width: 600px;
    height: 100%;
  }
  .Box4 {
    position: relative;
    margin: 0;
    opacity: 0;
    transform: translateX(200px);
    transition: 0.5s;
  }
  .Box4 img {
    width: 500px;
    height: 100%;
  }
  .Box3 {
    position: relative;
    margin: 0;
    opacity: 0;
    transform: translateX(-200px);
    transition: 0.5s;
  }
  .Box3 img {
    width: 600px;
    height: 100%;
  }
  .txtBox h3 {
    margin: 0 auto;
    max-width: 250px;
    font-size: 20px;
    font-weight: 300;
    border-bottom: 1px solid;
  }
  .txtBox {
    line-height: 1.5;
  }
  .txtBox h4 {
    font-size: 16px;
    width: 100%;
  }
  .stack {
    display: flex;
    justify-content: center;
  }

  .stack p {
    font-weight: 300;
    font-size: 14px;
    letter-spacing: 3px;
  }

  .date {
    display: block;
    margin-bottom: 0px;
  }
  .swiper {
    width: 80vw; /* 30% of the viewport width */
    max-width: 500px; /* 최대 너비 설정 */
    z-index: 1;
    --swiper-theme-color: #00000000;

    border: 1px solid;
  }
  .swiper2 {
    width: 80vw; /* 30% of the viewport width */
    max-width: 600px; /* 최대 너비 설정 */
    z-index: 1;
    --swiper-theme-color: #00000000;
    border: 1px solid;
  }
  .icon {
    position: relative;
    left: 0;
    font-size: 25px;
  }

  .allClick {
    font-size: 18px;
    cursor: pointer;
    position: absolute;
    text-align: center;
    top: 300px;
    left: 0;
    right: 0;
  }
  .txtBox {
    text-align: center;
    transform: translateY(-150px);
    transition: 0.5s;
    letter-spacing: 5px;
  }
}
