.intro {
  letter-spacing: 20px;
  height: 100vh;
}

.intro h2 {
  font-size: 3.12vw;
  font-weight: 200;
}

.introTxt {
  padding-top: 250px;
  line-height: 2;
}

.intro h1 {
  font-weight: 300;
  font-size: 3.12vw;
}
.intro strong {
  font-weight: 500;
  font-size: 4.16vw;
}
.strong1,
.strong2,
.strong3,
.strong4 {
  display: inline-block; /* 또는 display: block; */
}

.strong1 {
  animation: jump 1s ease-in-out infinite;
}

.strong2 {
  animation: jump 1s ease-in-out infinite;
  animation-delay: 0.1s; /* 0.1초 지연 */
}

.strong3 {
  animation: jump 1s ease-in-out infinite;
  animation-delay: 0.2s; /* 0.2초 지연 */
}

.strong4 {
  animation: jump 1s ease-in-out infinite;
  animation-delay: 0.3s; /* 0.3초 지연 */
}

@keyframes jump {
  50% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0px);
  }
}

@media screen and (max-width: 600px) {
  .intro {
    letter-spacing: 1.6vw;
    text-align: center;
    height: 80vh;
  }
  .intro h2 {
    font-size: 16px;
    font-weight: 200;
  }

  .introTxt {
    width: 100%;
    line-height: 2;
  }

  .intro h1 {
    font-weight: 300;
    font-size: 16px;
  }
  .intro strong {
    font-weight: 500;
    font-size: 22px;
  }
}
