.header {
  width: 100%;
  height: 100px;
  border-bottom: 0.1px solid;
  margin: 0 -90px;
  position: fixed;
  z-index: 10000;
  top: 0;
  background: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(4px);
}
.header h3 {
  font-size: 40px;
  line-height: 2.5;
  font-weight: 200;
  margin: 0 90px;
  letter-spacing: 10px;
}
@media screen and (max-width: 600px) {
  .header {
    height: 70px;
    margin: 0 -20px;
  }

  .header h3 {
    width: 150px;
    font-size: 18px;
    font-weight: 300;
    margin: 10px 20px;
    letter-spacing: 5px;
  }
}
